export const langTranslator = (engWord: string) => {
    let rusWord = engWord
    if (engWord === 'facebook') rusWord = ''
    if (engWord === 'website') rusWord = ''
    if (engWord === 'vk') rusWord = ' '
    if (engWord === 'twitter') rusWord = ''
    if (engWord === 'instagram') rusWord = ''
    if (engWord === 'youtube') rusWord = ''
    if (engWord === 'github') rusWord = ''
    if (engWord === 'mainLink') rusWord = ''
    return rusWord
}